.page-header__wrapper {
  width: 280px;
  padding: 0 20px;

  display: flex;
  flex-direction: column;
}

.page-header__logo-image {
  margin-right: 57px;
}

$tablet-width: 768px;
$desktop-width: 1440px;


@media (min-width: $tablet-width) {
  
  .page-header__wrapper {
    width: 708px;
    padding: 0 30px;
    margin: 0 auto;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  .page-header__logo-image {
    margin: 0;
  }
}

@media (min-width: $desktop-width) {
  .page-header__wrapper {
    width: 1220px;
    padding: 0 110px;
    margin: 0 auto;
  }
}

